<template>
    <div class="sidebar">
        <el-menu class="sidebar-el-menu" :default-active="onRoutes" :collapse="collapse" background-color="#324157"
            text-color="#bfcbd9" active-text-color="#20a0ff" unique-opened router>
            <template v-for="item in items">
                <template v-if="item.subs">
                    <el-submenu :index="item.index" :key="item.index">
                        <template slot="title">
                            <i :class="item.icon"></i><span slot="title">{{ item.title }}</span>
                        </template>
                        <el-menu-item v-for="(subItem,i) in item.subs" :key="i" :index="subItem.index">
                            {{ subItem.title }}
                        </el-menu-item>
                    </el-submenu>
                </template>
                <template v-else>
                    <el-menu-item :index="item.index" :key="item.index">
                        <i :class="item.icon"></i><span slot="title">{{ item.title }}</span>
                    </el-menu-item>
                </template>
            </template>
        </el-menu>
    </div>
</template>

<script>
    import bus from '../common/bus';
    export default {
        data() {
            return {
                collapse: false,
                items: [
                    {
                        icon: 'el-icon-rank',
                        index: 'dashboard',
                        title: this.$t("common.Home")
                    },
                    {
                        icon: 'el-icon-phone-outline',
                        index: '1',
                        title: this.$t("common.User_management_uesr"),
                        subs: [
                            {   
                                index: 'Personal_investment',
                                title: this.$t("common.Personal_investment")
                            },
                            {
                                index: 'Institutional_investment',
                                title: this.$t("common.Institutional_investment")
                            }
                        ]
                    },
                    {
                        icon: 'el-icon-view',
                        index: '2',
                        title: this.$t("common.Certification_Management"),
                        subs: [
                            {
                                index: 'Wait_list',
                                title: this.$t("common.Pending_certification_list")
                            },
                            {
                                index: 'Authentication_list',
                                title: this.$t("common.Certified_list")
                            },
                            {
                                index: 'Reject_list',
                                title: this.$t("common.Rejected_list")
                            }
                        ]
                    },
                    {
                        icon: 'el-icon-goods',
                        index: '3',
                        title: this.$t("common.Product_management"),
                        subs: [
                            {
                                index: 'Product_list',
                                title: this.$t("common.Pending_product_list")
                            },
                            {
                                index: 'Already_list',
                                title: this.$t("common.Added_product_list")
                            },
                            {
                                index: 'Down_list',
                                title: this.$t("common.Removed_product_list")
                            }
                        ]
                    },
                    {
                        icon: 'el-icon-tickets',
                        index: '4',
                        title: this.$t("common.Trade_management"),
                        subs: [
                            {
                                index: 'Trade_record',
                                title: this.$t("common.Transaction_Record")
                            },
                            {
                                index: 'Revenue_detail',
                                title: this.$t("common.Revenue_details")
                            },
                            {
                                index: 'Withdrawal_record',
                                title: this.$t("common.Funds_withdrawal_records")
                            }
                        ]
                    },
                    {
                        icon: 'el-icon-star-off',
                        index: '5',
                        title: this.$t("common.Rating_management"),
                        subs: [
                            {
                                index: 'Rating_list',
                                title: this.$t("common.Rating_enterprise_list")
                            },
                            {
                                index: 'Material_review',
                                title: this.$t("common.Material_review")
                            },
                            {
                                index: 'First_draft',
                                title: this.$t("common.First_draft")
                            },
                            {
                                index: 'Rating_publish',
                                title: this.$t("common.Rating_publish")
                            }
                        ]
                    },
                    {
                        icon: 'el-icon-document',
                        index: '6',
                        title: this.$t("common.Contract_agreement_management"),
                        subs: [
                            {
                                index: 'User_contract',
                                title: this.$t("common.User_contract")
                            },
                            {
                                index: 'Partner_contract',
                                title: this.$t("common.Partner_Contract")
                            }
                        ]
                    },
                    {
                        icon: 'el-icon-info',
                        index: '7',
                        title: this.$t("common.Partner_management"),
                        subs: [
                            {
                                index: 'Cooperative_enterprise',
                                title: this.$t("common.Partner_enterprise_list")
                            }
                        ]
                    },
                    {
                        icon: 'el-icon-document',
                        index: '8',
                        title: this.$t("common.Content_management"),
                        subs: [
                            {
                                index: 'Wait_article',
                                title: this.$t("common.Pending_articles")
                            },
                            {
                                index: 'Release_article',
                                title: this.$t("common.Release_article")
                            },
                            {
                                index: 'Reject_article',
                                title: this.$t("common.Rejected_articles")
                            },
                            {
                                index: 'Delete_article',
                                title: this.$t("common.Article_deleted")
                            }
                        ]
                    },
                    {
                        icon: 'el-icon-message',
                        index: '9',
                        title: this.$t("common.Message_management"),
                        subs: [
                            {
                                index: 'Release_message',
                                title: this.$t("common.Published_news")
                            },
                            {
                                index: 'Deleted_messages',
                                title: this.$t("common.Deleted_messages")
                            }
                        ]
                    },
                    {
                        icon: 'el-icon-service',
                        index: '10',
                        title: this.$t("common.Customer_Service_Management"),
                        subs: [
                            {
                                index: 'User_treating',
                                title: this.$t("common.User_feedback_treated")
                            },
                            {
                                index: 'User_treated',
                                title: this.$t("common.User_feedback_processed")
                            }
                        ]
                    },
                    {
                        icon: 'el-icon-setting',
                        index: '11',
                        title: this.$t("common.System_Management"),
                        subs: [
                            {
                                index: 'System_user',
                                title: this.$t("common.System_user")
                            },
                            {
                                index: 'System_role',
                                title: this.$t("common.System_role")
                            }
                        ]
                    }
                ]
            }
        },
        computed:{
            onRoutes(){
                return this.$route.path.replace('/','');
            }
        },
        created(){
            // 通过 Event Bus 进行组件间通信，来折叠侧边栏
            bus.$on('collapse', msg => {
                this.collapse = msg;
            })
        }
    }
</script>

<style scoped>
    .sidebar{
        display: block;
        position: absolute;
        left: 0;
        top: 70px;
        bottom:0;
        overflow-y: scroll;
    }
    .sidebar::-webkit-scrollbar{
        width: 0;
    }
    .sidebar-el-menu:not(.el-menu--collapse){
        width: 250px;
    }
    .sidebar > ul {
        height:100%;
    }
</style>



